.mainDiv {
    position: relative;
    min-height: 100vh;
}

.navbar {
    background-color: #333;
    height: 100px;
    color: whitesmoke;
    text-align: center;
    font-size: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.footer {
    position: absolute;
  bottom: 0;
   width:100%;
   height: 100px;
   font-size: 30px;
   display: flex;
   justify-content: center;
   align-items: center;
   background-color: #333;
   color: whitesmoke;
}

.imageCardDivStyle {
    display: flex;
    justify-content: center;
    align-items: center;
}

.imageCardImgStyle {
    padding: 0px;
    margin: 0px;
    border: 0px;
    display: block;
    max-width: 100%;
    height: auto;
}

